import React from 'react';
import { useForm } from 'react-hook-form';
import { RouteComponentProps } from 'react-router';
import { Col, CustomInput, Row } from 'reactstrap';
import { EditForm, EditViewProps, getSelectedItem, InputGroupInline, InputType, onlyNumber, WindowComponent } from 'summer';
import { AtividadeEconomica, CLASS_NAME } from '../../../../models/dtos/mlf/atividadeEconomica';

interface AtividadeEconomicaEditProps extends EditViewProps<AtividadeEconomica> {}

const AtividadeEconomicaEditView: React.FC<AtividadeEconomicaEditProps & RouteComponentProps> = props => {
  const selectedItem = getSelectedItem(props);
  const formProps = useForm<AtividadeEconomica>({ defaultValues: { aliquota: 0, ...selectedItem }, mode: 'onBlur' });

  const validadeExtras = () => {
    const erros = [];

    if (formProps.getValues().dtInicioEscrituracao > formProps.getValues().dtTerminoEscrituracao) {
      erros.push(' A Data de Término da Escrituração não pode ser menor que a Data de Início da Escrituração');
    }

    return erros;
  };

  return (
    <WindowComponent title="Manutenção de Atividade Economica">
      <EditForm formProps={formProps} editViewProps={props} dtoClassName={CLASS_NAME} validateExtras={validadeExtras}>
        <Row>
          <Col>
            <InputGroupInline
              formProps={{ ...formProps, validation: { required: true, maxLength: 8 } }}
              id="codigo"
              isPrependLabel={true}
              label="Código"
              placeholder="Informe o Código do CFOP"
            />
          </Col>
          <Col>
            <InputGroupInline
              formProps={{ ...formProps, validation: { ...onlyNumber(0, 3) } }}
              id="aliquota"
              isPrependLabel={true}
              label="Alíquota"
              placeholder="Informe a Alíquota"
            />
          </Col>
        </Row>
        <Row>
          <Col>
            <InputGroupInline
              formProps={{ ...formProps, validation: { required: true, maxLength: 350 } }}
              id="descricao"
              isPrependLabel={true}
              label="Descrição"
              placeholder="Informe a Descrição da Atividade Econômica"
              type={InputType.TEXT_AREA}
            />
          </Col>
        </Row>
        <Row>
          <Col>
            <InputGroupInline
              formProps={{ ...formProps, validation: { required: true } }}
              id="dtInicioEscrituracao"
              isPrependLabel={true}
              label="Data de início da Escrituração"
              placeholder="Informe a data de inicio"
              type={InputType.DATE}
            />
          </Col>
          <Col>
            <InputGroupInline
              formProps={{ ...formProps, validation: { required: true } }}
              id="dtTerminoEscrituracao"
              isPrependLabel={true}
              label="Data de Término da Escrituração"
              placeholder="Informe a data de Término"
              type={InputType.DATE}
            />
          </Col>
        </Row>
        <Row>
          <Col>
            <CustomInput type="switch" label="Serviço" name="servico" id="servico" innerRef={formProps.register} style={{ marginBottom: '16px' }} />
          </Col>
          <Col>
            <CustomInput
              type="switch"
              label="Atividade Econômica Desonerada"
              name="desonerada"
              id="desonerada"
              innerRef={formProps.register}
              style={{ marginBottom: '16px' }}
            />
          </Col>
        </Row>
      </EditForm>
    </WindowComponent>
  );
};

export default AtividadeEconomicaEditView;
